.pulse {
    stroke-dasharray: 1840;
    stroke-dashoffset: 3680;
    animation: dash 2s ease-out infinite;
    stroke: url(#strokeGradient);
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

span {
    color: #eee;
    display: block;
    text-align: center;
    font-size: 35px;
    margin-top: 15px;
    animation: blink 900ms linear infinite alternate;
}

@keyframes blink {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}